/*
// ***
//  Stylesheet App
//
//  Table of Contents:
//    1. Vars
//    2. Utils
//    3. Page
//    4. Size block
//    5. GitHub link
//    6. Footer
//
// ***
*/





/*
// 1. Vars
// ---
*/
:root {
	--color-white: #f4f5f6;
	--color-black: #212121;
	--color-correct-blue: #007aff;
	--default-font-size: 16;
	--min-font: calc(32/var(--default-font-size) * 1rem);
	--max-font: calc(128/var(--default-font-size) * 1rem);
}





/*
// 2. Utils
// ---
*/
.sr-only,
.accessible-hide {
	border: 0;
	clip: rect(0, 0, 0, 0);
	clip-path: inset(50%);
	height: 1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	white-space: nowrap;
	width: 1px;
}





/*
// 3. Page
// ---
*/
.app {
	background-color: var(--color-white);
	color: var(--color-black);
	display: flex;
	font-family: arial, sans-serif;
	font-size: calc(16/var(--default-font-size) * 1rem);
	margin: 0;
	min-height: 100vh;
	min-height: 100dvh;
	position: relative;
}

.app__main {
	font-size: clamp(var(--min-font), 1rem + 3vh, var(--max-font));
	margin: auto;
}

.app__link {
	color: inherit;
	text-decoration-color: var(--color-correct-blue);
}





/*
// 4. Size block
// ---
*/
.app__size-block {
	margin: 0;
	transform: translateY(calc(-16/var(--default-font-size) * 1rem));
}

.app__size-block + .app__size-block {
	margin-top: 1em;
}

.app__size-block--screen {
	position: relative;
}

.app__size-icon {
	aspect-ratio: 1;
	bottom: 0;
	height: 75%;
	left: 0;
	position: absolute;
	transform: translateX(-100%);
}

.app__size-icon::before {
	border: 2px solid var(--color-black);
	border-radius: 2px;
	content: '';
	height: 70%;
	left: 50%;
	position: absolute;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 40%;
}

.app__size-block--landscape .app__size-icon {
	margin-left: -5px;
	transform: translateX(-100%) rotate(90deg);
}

.app__size-title {
	display: block;
	font-size: .3em;
	opacity: .75;
	text-transform: uppercase;
}

.app__size-value {
	display: block;
	font-weight: bold;
}





/*
// 5. GitHub Link
// ---
*/
.app__code {
	font-size: calc(14/var(--default-font-size) * 1rem);
	margin: 0;
	position: absolute;
	right: 1em;
	top: 1em;
}

.app__code .app__link {
	text-decoration: none;
}

.app__code .app__link:hover {
	text-decoration: var(--color-correct-blue) underline;
}

.app__github {
	background: url(../img/screen-size/github.svg) left top no-repeat;
	display: inline-block;
	height: calc(1/var(--default-font-size) * 1rem);
	margin-left: .2em;
	overflow: hidden;
	padding-top: calc(23/var(--default-font-size) * 1rem);
	vertical-align: -3px;
	width: calc(24/var(--default-font-size) * 1rem);
}





/*
// 6. Footer
// ---
*/
.app__footer {
	background-color: #e4e5e6;
	border-radius: 4px 4px 0 0;
	bottom: 0;
	font-size: calc(14/var(--default-font-size) * 1rem);
	left: 50%;
	padding: 0 4rem;
	position: absolute;
	text-align: center;
	transform: translateX(-50%);
	white-space: nowrap;
}
